.footer {
	position: relative;
	padding: 236px 0 80px;
	overflow: hidden;
	@include md {
		padding: 220px 0 40px; }
	&:before {
		position: absolute;
		content: '';
		top: 72px;
		left: 0;
		width: 100%;
		height: 100%;
		transform-origin: right;
		transform: skewY(-3.3deg);
		background-image: linear-gradient(224deg, #EDF6FF 0%, #E2EFFF 100%); }
	&:after {
		position: absolute;
		content: '';
		z-index: -1;
		top: 14px;
		right: 0;
		width: 100%;
		height: 1px;
		transform-origin: right;
		transform: skewY(-2deg);
		background-image: linear-gradient(224deg, #DDECFD 0%, #C0D2FF 100%);
		@include md {
			display: none; } }
	.get-started {
		margin-top: -220px;
		margin-bottom: 40px; }
	.logo {
		@include sm {
			width: 114px; } }
	&__cirlce-left {
		position: absolute;
		left: 0;
		top: 330px;
		@include md {
			display: none; } }
	&__cirlce-right {
		position: absolute;
		top: 0;
		right: 0;
		@include md {
			display: none; } }
	&__inner {
		position: relative;
		z-index: 2; }
	&__row {
		margin: 0 -10px;
		display: flex;
		@include sm {
			flex-direction: column; }
		&:not(:last-child) {
			margin-bottom: 44px;
			@include sm {
				margin-bottom: 20px; } }
		&_inline {
			@include sm {
				flex-direction: initial; }
			@include sm {
				.footer__col-center,
				.footer__col-left {
					margin: 0;
					width: auto; }
				.footer__col-center {
					padding-left: 3px; }
				.footer__col-left {
					padding-right: 0; } } } }
	&__col-left {
		padding: 0 10px;
		width: 34%;
		flex-direction: column;
		justify-content: space-between;
		display: flex;
		@include sm {
			width: 100%; } }
	&__col-center {
		width: 40%;
		display: flex;
		@include sm {
			margin-top: 34px;
			padding: 0 10px;
			width: 100%; } }
	&__col {
		width: 40%;
		@include md {
			width: 50%; } }
	&__col-mob {
		@include sm {
			width: 50%; } }
	&__right {
		padding: 0 10px;
		width: 26%;
		@include sm {
			margin-top: 21px;
			width: 100%;
			display: flex; } }
	&__lang {
		align-items: center;
		display: inline-flex;
		@include sm {
			margin: 35px 0 10px; }
		span {
			margin-right: 10px;
			opacity: 0.5;
			font-weight: 500;
			font-size: 14px;
			color: #627791;
			letter-spacing: 0;
			line-height: 1.8; } }
	&__title {
		margin-bottom: 15px;
		font-size: 14px;
		color: #000000;
		font-weight: 700;
		letter-spacing: 0;
		line-height: 1.7; }
	&__list,
	&__soc {
		li {
			display: block;
			&:not(:last-child) {
				@include md {
					margin-bottom: 8px; } } }
		a {
			opacity: 0.8;
			font-weight: 500;
			font-size: 14px;
			color: #627791;
			letter-spacing: 0;
			line-height: 1.85;
			transition: opacity .3s;
			&:hover {
				opacity: 1; } } }
	&__soc {
		.icon {
			margin: 9px 10px 0 0;
			font-size: 16px;
			vertical-align: top;
			display: inline-block;
			@include md {
				margin: 4px 5px 0 0; } } } }
.copy,
.res, {
	opacity: .5; }
.copy,
.res,
.footer__mail {
	font-size: 14px; }
.footer__mail {
	transition: opacity .3s;
	@include hover {
		opacity: .5; } }
