html {
	&.is-open-menu {
		overflow-y: scroll; } }

body {
	@include fr;
	@include f(16px,400,2.0625);
	color: #627791;
	min-width: 320px;
	background-color: #F2F8FF;
	@include md {
		font-size: 14px;
		line-height: 1.85; }
	.is-open-menu & {
		position: fixed;
		height: 100vh;
		width: 100%;
		overflow: hidden; } }

.title-lg {
	color: #000;
	@include f(84px,700,1.07);
	@include md {
		@include f(54px,700,1.6); } }
.title-md {
	color: #000;
	@include f(50px,700,1.2);
	@include md {
		@include f(30px,700,1.26); } }
.title {
	color: #000;
	@include f(36px,700,1.5);
	@include md {
		@include f(24px,700,1.6); } }

.description {
	@include f(18px,500,2.2);
	@include md {
		font-size: 16px;
		line-height: 1.875; } }

.color-blue {
	color: #3852CE; }

.text-center {
	text-align: center; }

.section {
	position: relative;
	padding: 178px 0;
	@include md {
		padding: 80px 0; }
	&_about {
		padding-bottom: 0; }
	&__wrap {
		max-width: 488px;
		@include md {
			padding-right: 20px;
			max-width: 100%; }
		br {
			@include md {
				display: none; } } }
	&__row {
		margin: 0 -10px;
		display: flex;
		@include md {
			flex-direction: column;
			justify-content: center; }
		&:not(:first-child) {
			margin-top: 103px;
			@include md {
				margin-top: 80px; } } }
	&__img {
		padding: 0 10px;
		width: 50%;
		@include md {
			margin-bottom: 30px;
			order: -1;
			width: auto;
			max-width: 100%; }
		img,
		svg {
			display: block;
			height: auto;
			max-width: 100%; }
		&_shad {
			img {
				border-radius: 5px;
				box-shadow: 0 20px 40px 0 rgba(107,154,212,0.10); } }
		&_fixw {
			img {
				max-width: 960px;
				@include md {
					max-width: 140%; } } }
		&_center {
			justify-content: center;
			display: flex; } }
	&__content {
		padding: 38px 10px 0;
		width: 50%;
		@include md {
			padding: 0 10px;
			width: 100%; }
		&_pt0 {
			padding-top: 0; }
		p {
			&:not(:first-child) {
				margin-top: 17px;
				@include md {
					margin-top: 7px; } }
			&:not(:last-child) {
				margin-bottom: 29px;
				@include md {
					margin-bottom: 17px; } } }
		.description + .btn {
			margin-top: 29px; } }
	.title {
		margin-bottom: 11px;
		@include md {
			margin-bottom: 6px; } }
	.description {
		margin-bottom: 15px;
		@include md {
			margin-bottom: 7px; } }

	.poligon-right {
		position: absolute;
		top: 50%;
		right: -10px;
		margin-top: -120px;
		width: 160px;
		@include md {
			display: none; }
		img {
			display: block;
			max-width: 100%; } }
	.poligon-left {
		position: absolute;
		top: 40%;
		left: 0;
		width: 160px;
		@include md {
			display: none; }
		img {
			display: block;
			max-width: 100%; } }
	.right-circle {
		position: absolute;
		z-index: 2;
		top: 30px;
		right: -10px;
		@include md {
			display: none; } } }

.md-show {
	display: none !important;
	@include md {
		display: block !important; } }
.md-hide {
	@include md {
		display: none !important; } }

.sm-show {
	display: none !important;
	@include sm {
		display: block !important; } }
.sm-hide {
	@include sm {
		display: none !important; } }
